@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@100&display=swap);
.coin-container {
  display: flex;
  justify-content: center;
}

.coin-row {
  display: flex;
  flex-direction: row;
  justify-items: start;
  align-items: center;
  height: 80px;
  border-bottom: 1px solid goldenrod;
  width: 900px;
}

.coin {
  display: flex;
  align-items: center;
  padding-right: 24px;
  min-width: 200px;
}

.coin h1 {
  font-size: 16px;
  width: 150px;
  color: gold;
}

.coin img {
  height: 30px;
  width: 30px;
  margin-right: 10px;
}

.coin-symbol {
  text-transform: uppercase;
}

.coin-data {
  display: flex;
  text-align: center;
  justify-content: space-between;
  width: 100%;
}

.coin-price {
  width: 110px;
}

.coin-volume {
  width: 200px;
}

.coin-marketcap {
  width: 240px;
}

.coin-percent {
  width: 80px;
}

.red {
  color: rgb(255, 0, 0);
}

.green {
  color: rgb(6, 179, 6);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "JetBrains+Mono:wght@100", sans-serif;
  background-color: #0b0425;
  color: rgb(113, 67, 219);
}

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: gold;
}

.coin-search {
  margin-bottom: 54px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.coin-text {
  margin-bottom: 32px;
  text-align: center;
  color: gold;
}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(
    -225deg,
    #ac32e4 0%,
    #7918f2 48%,
    #4801ff 100%
  );
  color: aliceblue;
}

.coin-input::-webkit-input-placeholder {
  color: aliceblue;
}

.coin-input:-ms-input-placeholder {
  color: aliceblue;
}

.coin-input::placeholder {
  color: aliceblue;
}

